/* Global Variables */
/* Colors */
html[data-theme="1"] {
  --main-text-color: hsl(221, 14%, 31%);
  --second-text-color: hsl(0, 0%, 100%);
  --third-text-color: hsl(0, 0%, 100%);
  --screen-bg-color: hsl(222, 26%, 31%);
  --second-bg-color: hsl(224, 36%, 15%);
  --main-bg-color: hsl(223, 31%, 20%);
  --main-key-bg-color: hsl(30, 25%, 89%);
  --main-key-shadow-color: hsl(28, 16%, 65%);
  --second-key-bg-color: hsl(225, 21%, 49%);
  --second-key-shadow-color: hsl(224, 28%, 35%);
  --third-key-bg-color: hsl(6, 63%, 50%);
  --third-key-shadow-color: hsl(6, 70%, 34%);
}

html[data-theme="2"] {
  --main-text-color: hsl(60, 10%, 19%);
  --second-text-color: hsl(0, 0%, 100%);
  --third-text-color: hsl(0, 0%, 100%);
  --screen-bg-color: hsl(0, 0%, 93%);
  --main-bg-color: hsl(0, 0%, 90%);
  --second-bg-color: hsl(0, 5%, 81%);
  --main-key-bg-color: hsl(45, 7%, 89%);
  --main-key-shadow-color: hsl(35, 11%, 61%);
  --second-key-bg-color: hsl(185, 42%, 37%);
  --second-key-shadow-color: hsl(185, 58%, 25%);
  --third-key-bg-color: hsl(25, 98%, 40%);
  --third-key-shadow-color: hsl(25, 99%, 27%);
}

html[data-theme="3"] {
  --main-text-color: hsl(52, 100%, 62%);
  --second-text-color: hsl(0, 0%, 100%);
  --third-text-color: hsl(198, 20%, 13%);
  --screen-bg-color: hsl(268, 71%, 12%);
  --main-bg-color: hsl(268, 75%, 9%);
  --second-bg-color: hsl(268, 71%, 12%);
  --main-key-bg-color: hsl(281, 89%, 26%);
  --main-key-shadow-color: hsl(285, 91%, 52%);
  --second-key-bg-color: hsl(268, 47%, 21%);
  --second-key-shadow-color: hsl(290, 70%, 36%);
  --third-key-bg-color: hsl(176, 100%, 44%);
  --third-key-shadow-color: hsl(177, 92%, 70%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
*:focus {
  outline: none;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  background-color: transparent;
}

img {
  user-select: none;
  border-style: none;
}

input,
select,
textarea,
button,
optgroup {
  font-size: inherit;
  font-family: inherit;
  color: inherit;
}

button {
  border: none;
  cursor: pointer;
}

body {
  background-color: var(--screen-bg-color);
  font-size: 32px;
  font-family: "League Spartan", sans-serif;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 768px) {
  .container {
    width: 100%;
  }
}
@media (min-width: 768.1px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992.1px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200.1px) {
  .container {
    width: 1170px;
  }
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-track {
  background-color: var(--main-text-color);
}

::-webkit-scrollbar-thumb {
  cursor: pointer;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb, ::-webkit-scrollbar-thumb:hover {
  background-color: var(--main-bg-color);
}

main {
  width: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--main-text-color);
  padding: 20px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--second-text-color);
}
header h1 {
  font-size: 24px;
}
header > div {
  display: flex;
  align-items: center;
  gap: 20px;
  text-transform: uppercase;
  font-size: 12px;
}
header > div > span {
  letter-spacing: 0.5px;
}
header > div .theme-control {
  background-color: var(--main-bg-color);
  height: min-content;
  display: flex;
  gap: 5px;
  padding: 5px;
  border-radius: 20px;
}
header > div .theme-control button {
  height: 11px;
  width: 11px;
  border-radius: 50%;
  background-color: var(--third-key-bg-color);
  position: relative;
  counter-increment: theme;
}
header > div .theme-control button::before {
  content: counter(theme);
  position: absolute;
  left: 50%;
  top: -5px;
  transform: translate(-50%, -100%);
}

section.keypad {
  background-color: var(--second-bg-color);
  color: var(--second-text-color);
  padding: 30px 25px;
  border-radius: 6px;
  text-align: end;
  font-size: 40px;
  margin: 25px 0 20px;
}

section.keys {
  background-color: var(--main-bg-color);
  border-radius: 6px;
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 20px;
}
section.keys .natural-numbers {
  grid-area: 1/1/4/4;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
section.keys button {
  border-radius: 6px;
  padding: 10px;
  background-color: var(--main-key-bg-color);
  box-shadow: inset 0 -3px 0px var(--main-key-shadow-color);
}
section.keys button.reset {
  grid-area: 5/1/6/3;
}
section.keys button.equal {
  background-color: var(--third-key-bg-color);
  box-shadow: inset 0 -3px 0px var(--third-key-shadow-color);
  grid-area: 5/3/6/5;
  color: var(--second-text-color);
}
section.keys button.btn-text {
  background-color: var(--second-key-bg-color);
  box-shadow: inset 0 -3px 0px var(--second-key-shadow-color);
  text-transform: uppercase;
  color: var(--second-text-color);
  font-size: 22px;
}